.media-left,
.media > .pull-left {
  padding-right: 8px;

  @include MQ($small) {
    padding-right: 20px;
  }

}

.media, .media-body {
  overflow: visible;
}

.media-body .form-group {
  margin-bottom: 0;
}

.media-heading {
  display: inline-block;
  margin-top: 3px;
  margin-bottom: 0;
}

.input-group {
  max-width: 23rem;
}
